
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import { validateNotNull } from 'util/rules'

export default {
data () {
return {
stock: {},
originStock: {},
ifPop: false,
successImg: require('../../assets/images/success_img.png')
}
},
methods: {
toHome () {
this.$router.push({
path: '/stockList'
})
},
toDetail () {
this.$router.push({
path: '/stockView',
query: {
stockId: this.stock.id
}
})
},
_getStockProductClientDraft () {
this.$tupHttp(this, 'front/stockProduct/getStockProductClientDraft', 'POST').then(res => {
this.stock = res.data.model
this.originStock = this.$util.deepClone(res.data.model)
if (this.stock.stockProductClientDetailList) {
this.stock.stockProductClientDetailList.push({
productName: '其他产品',
price: '-',
shopCount: '-',
storehouseCount: '-',
totalCount: '-',
stockMoney: this.stock.otherMoney
})
}
})
},
_submit () {
if (validateNotNull(this, this.originStock.manageArea, '经营面积') && validateNotNull(this, this.originStock.monthRent, '店月租金') && validateNotNull(this, this.originStock.monthSalary, '月人工工资')) {
if (this.originStock.manageArea && (Number(this.originStock.manageArea) <= 0 || Number(this.originStock.manageArea) > 200)) {
this.$vux.toast.text('经营面积需为0-200m²')
return
}
if (this.originStock.monthRent && (Number(this.originStock.monthRent) <= 0 || Number(this.originStock.monthRent) > 50000)) {
this.$vux.toast.text('店月租金需为0-50000元')
return
}
if (this.originStock.monthSalary && (Number(this.originStock.monthSalary) <= 0 || Number(this.originStock.monthSalary) > 50000)) {
this.$vux.toast.text('月人均工资需为0-50000元')
return
}
let ifValid = this.originStock.stockProductClientDetailList.some(item => {
return (item.shopCount && (item.shopCount < 0 || !/^\d+$/.test(item.shopCount))) || (item.storehouseCount && (item.storehouseCount < 0 || !/^\d+$/.test(item.storehouseCount)))
}) // 库存填报数据，如果有填，需大于0
if (ifValid) {
this.$vux.toast.text('库存填报数据须为>=0的整数')
return
}
if (this.originStock.otherMoney && !/^\d+$/.test(this.originStock.otherMoney)) {
this.$vux.toast.text('其他产品总金额需要为>=0的整数')
return
}
if (validateNotNull(this, this.originStock.inventoryUser, '盘点人') && validateNotNull(this, this.originStock.superviseUser, '监察人') && validateNotNull(this, this.originStock.clientUser, '店主')) {
this.originStock.stockProductClientDetailList.map(item => {
if (item.shopCount === null || item.shopCount === '') {
item.shopCount = 0
}
if (item.storehouseCount === null || item.storehouseCount === '') {
item.storehouseCount = 0
}
return item
})
this.originStock.otherMoney = this.originStock.otherMoney === null || this.originStock.otherMoney === '' ? 0 : this.originStock.otherMoney
this.$tupHttp(this, 'front/stockProduct/submit', 'POST', this.originStock).then(res => {
this.ifPop = true
})
}
}
}
},
created () {
this._getStockProductClientDraft()
},
components: {
XHeader,
Popup
}
}



